<script>
import beian from './beian'
import axios from "axios";
// import APlayer from 'vue-aplayer'

export default {
  components: {
    beian,
    // APlayer
  },
  data() {
    return {
      centerDialogVisible:false,
      centerDialogHref:false,
      audioList: {
        meting_music_api: "https://api.mizore.cn/meting/api.php",// meting api
        music_server: "netease", // 音乐服务：网易云 netease 参见 meting api支持的服务
        music_type: "playlist", // 歌曲类型：歌单playlist 单首歌曲 song  专辑 album
        music_id: "8261747919", // 歌单ID或歌曲ID或者专辑ID 20173709 29732992
        music_order: "random", // 音频循环顺序, 顺序播放： 'list', 随机播放：'random'
        music_mini: false, // mini模式 吸底模式开启后此选项实效 true / false
        music_fixed: false, // 吸底模式
        music_volume: 0.7, // 默认音量
        music_autoplay: false, // 自动播放
        music_loop: "all" // 音频循环播放, 可选值: 'all', 'one', 'none' ，分别为全部循环，单曲循环，不循环
      },
      aPlayerObj: null,
      audio: [
        {
          title: "海に行く",
          artist: "茶太",
          src: "/resources/wumiyiku.mp3",
          lrc: "",
          theme: "#aaf2ff",
        }
      ]
    }
  },
  methods: {
    btnGroupClick(href) {
      this.centerDialogHref = href
      this.centerDialogVisible = true
    }
  },
  mounted() {
    let that = this
    axios.get(that.audioList.meting_music_api, {
      params: {
        server: that.audioList.music_server,
        type: that.audioList.music_type,
        id: that.audioList.music_id
      }
    }).then(function (response) {
      if (response.data) {
        let audio = response.data;
        // eslint-disable-next-line no-undef
        that.aPlayerObj = new APlayer({
          container: that.audioList.music_fixed === false ?
              document.getElementById('aplayer-inner') : document.getElementById('aplayer-fixed'),
          audio: audio,
          fixed: that.audioList.music_fixed !== false,
          autoplay: that.audioList.music_autoplay,
          order: that.audioList.music_order,
          listFolded: true,
          volum: that.audioList.music_volume,
          mini: that.audioList.music_fixed === true ? true : that.audioList.music_mini,
          lrcType: 3,
          preload: "auto",
          loop: that.audioList.music_loop
        });
      }
    });
  }
}
</script>
